import React from "react"

import Table from 'react-bootstrap/Table';
import { Heading } from "../../../components/heading";

const Date = () => {
  return (
    <div>
      <Heading as="h2" >日時</Heading>
      4月4日(木) ～ 4月6日(土)の開催を予定しています。
      <br />
      また、4月8日(月)以降にもイベントを開催する可能性があります。
      <Heading as="h2" >詳細</Heading>
      ピクトラボ、および西9号館の1階CED、135または335にて開催予定です。<br />
      それぞれの場所では各団体のブースを設け、展示や個別説明を行う予定です。
    </div>
  )
}

export default Date